import VueRouter from 'vue-router'
import DashBoard from './components/DashBoard'
import MyTasks from './components/MyTasks'
import TestData from './components/TestData'

const router = new VueRouter({
    routes: [
      {
        name: 'dashboard',
        path: '/dashboard',
        component: DashBoard
      },
      {
        name: 'tasks',
        path: '/tasks',
        component: MyTasks
      },
      {
        name: 'testdata',
        path: '/testdata',
        component: TestData
      }
    ],
    mode: 'history'
  })
export default router;
    